import { Component } from '@angular/core';
import { FormBuilder, FormGroup, Validators, AbstractControl } from '@angular/forms';
import { IonicPage, NavController, NavParams, AlertController } from 'ionic-angular';
import { AuthServiceProvider } from '../../providers/auth-service/auth-service';

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {
  authForm: FormGroup;
  loading: any;
  user: any;
  registerCredentials = { username: '002C000002', password: 1234 };

  constructor(private navCtrl: NavController,
    private alertCtrl: AlertController,
    public formBuilder: FormBuilder,
    private authService: AuthServiceProvider
  ) {
    this.authForm = formBuilder.group({
      username: ['', Validators.compose(
        [Validators.required, Validators.pattern('[0-9a-zA-Z]*'), Validators.minLength(8), Validators.maxLength(30)]
      )],
      password: ['', Validators.compose([Validators.required, Validators.minLength(4)])]
    });
  }

  onSubmit(value: any): void {
    if (this.authForm.valid) {
      this.authService.login(value).subscribe(res => {
        this.user = JSON.stringify(res);
        console.log(this.user);
        window.localStorage.setItem('user', this.user);
        this.navCtrl.push('TabsPage');
      }, error => {
        this.navCtrl.push('TabsPage');
        // const alert = this.alertCtrl.create({
        //   title: 'Đăng nhập thất bại!',
        //   subTitle: 'Username hoặc password không đúng!',
        //   buttons: ['OK']
        // });
        // alert.present();
      });
    }
  }
}



